.container-loader {
  min-width: 320px;
  max-width: 375px;
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;
  // outline: 1px solid tomato;

  @media screen and (min-width: 768px) {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
  @media screen and (min-width: 1440px) {
    max-width: 1440px;
    padding-left: 24px;
    padding-right: 24px;
  }
}
.container {
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 768px) {
  .support-categories-container {
    display: flex;
    justify-content: space-between;
    margin-top: 40px;
  }
  .container {
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .support-categories-container {
    display: flex;
    flex-direction: column;
    justify-content: start;
    margin-top: 40px;
    gap: 86px;
  }
  .container-home-page {
    display: flex;
  }
  .container {
    width: 1440px;
    padding-left: 24px;
    padding-right: 24px;
  }
}
